const searchBtn = document.querySelector("#search-btn");

if (document.querySelector("#filters") !== null) {
  let inputCity;
  let inputCountry;
  document.addEventListener('DOMContentLoaded', () => {  //    
    inputCity = document.getElementById('city');         //     
    inputCountry = document.getElementById('country');
    inputCountry.value = localStorage.getItem('savedDataCountry') || ''; //      localStorage,   (  . .)
    inputCity.value = localStorage.getItem('savedDataCity') || '';
    searchBtn.addEventListener('click', () => {                     //     ""...
      localStorage.setItem('savedDataCountry', inputCountry.value); // ... ,  localStorage  /    "savedData"    inputData (  )
      localStorage.setItem('savedDataCity', inputCity.value);
    });
  });
};
